

* {
    box-sizing: border-box;
}
html{
    height: 100%;
}

body {
    margin: 0;
    padding: 0;
    font-family: "Segoe UI";
    /*background: #f8f9fb;*/
    background: #FFFFFF;
    box-sizing: border-box;
    height: 100%;
}

.container {
    width: 100%;
    padding-left: 25px;
    padding-right: 25px;
    margin-left: auto;
    margin-right: auto;
}


@font-face {
	font-family: 'Segoe UI';
	src: url(/static/media/SegoeUI-Light.db415e98.eot);
	src: local('./SegoeUI/Segoe UI Light'), local('SegoeUI-Light'),
		url(/static/media/SegoeUI-Light.db415e98.eot?#iefix) format('embedded-opentype'),
		url(/static/media/SegoeUI-Light.02b73ca1.woff) format('woff'),
		url(/static/media/SegoeUI-Light.b34ee12d.ttf) format('truetype');
	font-weight: 300;
	font-style: normal;
}

@font-face {
	font-family: 'Segoe UI';
	src: url(/static/media/SegoeUI-BoldItalic.6b9f4bbb.eot);
	src: local('Segoe UI Bold Italic'), local('SegoeUI-BoldItalic'),
		url(/static/media/SegoeUI-BoldItalic.6b9f4bbb.eot?#iefix) format('embedded-opentype'),
		url(/static/media/SegoeUI-BoldItalic.7c08f513.woff) format('woff'),
		url(/static/media/SegoeUI-BoldItalic.c040af57.ttf) format('truetype');
	font-weight: bold;
	font-style: italic;
}

@font-face {
	font-family: 'Segoe UI';
	src: url(/static/media/SegoeUI-SemiBold.cb5a10bc.eot);
	src: local('Segoe UI Semibold'), local('SegoeUI-SemiBold'),
		url(/static/media/SegoeUI-SemiBold.cb5a10bc.eot?#iefix) format('embedded-opentype'),
		url(/static/media/SegoeUI-SemiBold.1b14ce9a.woff) format('woff'),
		url(/static/media/SegoeUI-SemiBold.310d7546.ttf) format('truetype');
	font-weight: 600;
	font-style: normal;
}

@font-face {
	font-family: 'Segoe UI';
	src: url(/static/media/SegoeUI.03bcc7ef.eot);
	src: local('Segoe UI'), local('SegoeUI'),
		url(/static/media/SegoeUI.03bcc7ef.eot?#iefix) format('embedded-opentype'),
		url(/static/media/SegoeUI.ee10411f.woff) format('woff'),
